* {
  box-sizing: border-box;
}

body {
  color: #fff;
  background-color: rgba(5, 0, 0, .765);
  font-family: sans-serif;
  line-height: 1.5;
}

.gallery {
  flex-flow: wrap;
  justify-content: center;
  gap: 25px;
  display: flex;
}

.photo-card {
  width: 280px;
  height: auto;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  box-shadow: 1px 5px 5px #f7f2f2;
}

.photo-card img {
  width: 100%;
  height: 200px;
}

.info {
  text-align: center;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
  padding: 0 10px 13px;
  display: flex;
}

.info-item {
  margin: 0;
  font-size: 13px;
}

.search-container {
  background-color: #bdd1d5;
  justify-content: center;
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
}

.search-form {
  width: 100%;
  max-width: 400px;
  border-radius: 10px;
  align-items: center;
  gap: 10px;
  display: flex;
}

input {
  color: #fff;
  background-color: rgba(5, 0, 0, .765);
  border: none;
  border-radius: 5px;
  flex: 1;
  padding: 10px;
  font-size: 16px;
}

search-form input:focus {
  outline: none;
}

button {
  cursor: pointer;
  color: rgba(5, 0, 0, .765);
  background-color: #fff;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
  font-weight: 700;
}

button:focus, button:hover {
  color: #fff;
  background-color: rgba(5, 0, 0, .765);
  outline: none;
}

.load-more {
  text-align: center;
  margin: 50px auto 0;
  display: block;
}

.load-more.hidden {
  display: none;
}

.load-more:disabled {
  cursor: not-allowed;
  color: initial;
  background-color: #fff;
}

.load-more:disabled:hover, .load-more:disabled:focus {
  color: initial;
  box-shadow: none;
  background-color: #fff;
  outline: none;
}

